import { fetchJson, fetchJsonStream, } from 'spatial-id-svc-base';
/** 空間 ID の範囲内の予約ルートを複数取得する */
export const getReservedRoutes = async function* ({ baseUrl, authInfo, payload, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'POST',
        baseUrl,
        path: '/route_service/reserved_routes_list',
        authInfo,
        payload,
        abortSignal,
    })) {
        yield chunk;
    }
};
/** ID を指定して予約ルートを 1 件取得する */
export const getReservedRoute = async ({ baseUrl, authInfo, reservedRouteId, abortSignal, }) => {
    return await fetchJson({
        method: 'GET',
        baseUrl,
        path: `/route_service/reserved_routes/${encodeURIComponent(reservedRouteId)}`,
        authInfo,
        abortSignal,
    });
};
/** ID を指定して予約ルートを削除する */
export const deleteReservedRoute = async ({ baseUrl, authInfo, reservedRouteId, abortSignal, }) => {
    await fetchJson({
        method: 'DELETE',
        baseUrl,
        path: `/route_service/reserved_routes/${encodeURIComponent(reservedRouteId)}`,
        authInfo,
        abortSignal,
    });
};
