import { ApiResponseError, fetchJson, fetchJsonStream, fetchRawJson, } from 'spatial-id-svc-base';
/** 空間 ID の範囲内のパブリックバリアを複数取得する */
export const getBarriers = async function* ({ baseUrl, authInfo, payload, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'POST',
        baseUrl,
        path: '/route_service/barriers_list',
        authInfo,
        payload,
        abortSignal,
    })) {
        yield chunk;
    }
};
/** ID を指定してパブリックバリアを 1 件取得する */
export const getBarrier = async function* ({ baseUrl, authInfo, id, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'GET',
        baseUrl,
        path: `/route_service/barriers/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    })) {
        yield chunk;
    }
};
/** パブリックバリアを生成する */
export const createBarrier = async ({ baseUrl, authInfo, payload, abortSignal, }) => {
    const resp = await fetchRawJson({
        method: 'POST',
        baseUrl,
        path: '/route_service/barriers',
        authInfo,
        payload,
        abortSignal,
    });
    if (resp.id === '0') {
        throw new ApiResponseError('failed to create: returned id is 0');
    }
    return resp;
};
/** ID を指定してパブリックバリアを削除する */
export const deleteBarrier = async ({ baseUrl, authInfo, id, abortSignal, }) => {
    await fetchJson({
        method: 'DELETE',
        baseUrl,
        path: `/route_service/barriers/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    });
};
